
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'

import { mapState, mapActions } from 'vuex'
import HttpSrv from '../service/http-service'
const httpSrv = new HttpSrv();

export default {
components: {
XInput,
XButton,
Group,
Box
},
methods: {
async onUserBind() {
console.log('phone:', this.userPhone, 'openid:', this.auth.openid);
if (!this.userPhone) {
this.$vux.toast.text('请输入手机号', 'middle');
return;
}

let userBind = await httpSrv.init().setPath('/wechatBind')
.setBody({
phone: this.userPhone,
openId: this.auth.openid
})
.showLoader()
.postReq();

if (userBind.errno === 0) {
this.$vux.toast.show({
type: 'success',
text: userBind.msg,
position: 'middle'
});
location.href = `index.html?tag=app&${querystring.stringify(this.auth)}`;
} else {
this.$vux.toast.show({
type: 'warn',
text: userBind.msg,
position: 'middle'
});
}
}
},
computed: {
...mapState({
mainMenu: state => state.app.mainMenu,
user: state => state.data.user,
auth: state => state.data.auth
})
},
data() {
return {
userPhone: null
}
},
created() {

}
}



