
import Badge from 'vux/src/components/badge/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState, mapActions } from 'vuex'

export default {
name: 'app',
directives: {
TransferDom
},
components: {
Badge,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet
},
methods: {
onClickMore () {
this.showMenuAction(this.path);
console.log(`mainMenu path: ${this.path} show: ${this.appStore.mainMenu.show}`);
},
...mapActions([
'updateDemoPosition',
'showRightOptionAct',
'setMenuPathAction',
'showMenuAction',
'setUserData'
])
},
watch: {
path (path) {



}
},
computed: {
...mapState({
route: state => state.route,
path: state => state.route.path,
appStore: state => state.app,
isLoading: state => state.app.isLoading,
direction: state => state.app.direction,
headerTitle: state => state.app.headerTitle,
showRightOpt: state => state.app.rightOption,
dataStore: state => state.data,
user: state => state.data.user
}),
leftOptions () {
return {
showBack: !/tab/.test(this.route.path)
}
},
rightOptions () {
return {
showMore: this.showRightOpt
}
},
headerTransition () {
if (this.direction) {
return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
} else {
return ''
}
},
isQuickView () {
return /quick/.test(this.route.path)
},
isShowTabBar () {
return /tab/.test(this.route.path)
},
title () {
return this.headerTitle || this.route.name;
},
viewTransition () {
if (this.direction) {
return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out')
} else {
return ''
}
}
},
data () {
return {

}
},
mounted () {

},
beforeDestroy () {

}
}



