
import UploaderItem from './uploader-item.vue'
import HttpService from '../service/http-service'
const httpSrv = new HttpService();
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import lrz from 'lrz'

export default {
props: {
images: {
type: Array,
default: () => []
},
max: {
type: Number,
default: 1
},
showHeader: {
type: Boolean,
default: true
},
showTip: {
type: Boolean,
default: true
},
readonly: {
type: Boolean,
default: false
},
title: {
type: String,
default: '图片上传'
},

handleClick: {
type: Boolean,
default: false
},

autoUpload: {
type: Boolean,
default: true
},
uploadUrl: {
type: String,
default: ''
},
size: {
type: String,
default: 'normal'
},
capture: {
type: String,
default: ''
},
name: {
type: String,
default: 'img'
},
params: {
type: Object,
default: null
}
},
directives: {
TransferDom
},
components: {
UploaderItem,
Previewer
},
methods: {
add () {
if (this.handleClick) {
this.$emit('add-image')
}
},

remove () {
if (this.handleClick) {
this.$emit('remove-image')
} else {
this.images.shift()
}
},
preview (index) {

this.$refs.previewer.show(index);
},

async change () {
if (this.handleClick) {
return
}
let _self = this;
if (_self.$vux && _self.$vux.loading) {
_self.$vux.loading.show('正在上传')
}
console.log("原始图片大小:"+(this.$refs.input.files[0].size)/1024+"K");
/**
* 图片压缩参数
* @param width：设置图片最大宽度  quality:比例默认0.7
* by weijin
*/
let maxSize = 200 * 1024; 
let imgSize = this.$refs.input.files[0].size;
let lrzParams = {};
if (imgSize > maxSize) {
lrzParams.width = 800;

}

lrz(this.$refs.input.files[0],lrzParams).then(async function (rst) {
console.log("压缩后图片大小:"+rst.file.size/1024+"K");
let formData = new window.FormData();
formData.append(_self.name, rst.file);
if (_self.params) {
for( let key in _self.params) {
formData.append(key, _self.params[key])
}
}
if (_self.autoUpload) {
if (!_self.uploadUrl) {
console.error('uploadUrl不应为空')
}
let response = await httpSrv.init().setPath('/core/order/OrderMain')
.setQuery({
method: 'uf',
type:_self.params.type,
order_id:_self.params.order_id
})
.setBody(formData)
.postReq();

if (response.errno === 0) {
if (_self.$vux && _self.$vux.loading) {
_self.$vux.loading.hide()
}
_self.$emit('upload-finish', response.info.file_id);
_self.$refs.input.value = '';
let imageUrl = httpSrv.init().setPath('/core/file/UploadFile?method=gf').reqUrl
+'&type='+_self.params.type+'&order_id='+_self.params.order_id+'&random='+Math.random();
_self.images.push({url: imageUrl})
_self.list.push({
src: imageUrl
})
}
} else {
_self.$emit('upload-image', formData)
}
});
}
},
data () {
return {
list: []
}
},
computed: {
showCapture () {
return this.capture || undefined
}
},
watch: {
images() {
this.list = [];
for (let i in this.images) {
this.list.push({
src: this.images[i].url
})
}
}
}
}



